	import {  
        mapMutations  
    } from 'vuex';
	export default{
		data(){
			return {
				seller_name: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			//uni.hideTabBar();
		},
		methods: {
			...mapMutations(['setSellerToken']),
			async toLogin(){
				this.logining = true;
				this.$Request.post('seller_login',{seller_name:this.seller_name,password:this.password,client:'wap'}).then(res => {
					if (res.code == 200) {
						console.log(res);
						this.setSellerToken(res.datas.stoken);
						//this.setUserInfo(res.datas);
						
						uni.redirectTo({
							url:"/pages/seller/seller"
						}) 
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						this.logining = false;
					}
				});
			},
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			}
		},

	}
